import React from "react"
import Layout from "../../components/layout"
import SEO from "../../components/seo"


const PulseraAmiga = (dataState) => (
  
  <Layout>
    <SEO  
    title="Hazle una pulsera de la amistad a tu amiga"
    meta="Te explicamos que tipos de pulseras le puedes hacer a tu amiga! ¿No sabes que regalarle? Te explicamos que tipos de pulseras puedes hacerle y como hacerlas!"
    description="Te explicamos que tipos de pulseras le puedes hacer a tu amiga! ¿No sabes que regalarle? Te explicamos que tipos de pulseras puedes hacerle y como hacerlas!"
    lang="es"
    />
    <div class="postContainer">
    <p>&nbsp;</p>
<h1>Hazle una pulsera de la amistad</h1>
<p>Si te gustan las manualidades las pulseras de la amistad son un regalo sencillo que puedes hacerle. Si le cuentas que la has hecho tu lo tendr&aacute; en cuenta y le har&aacute; especial ilusi&oacute;n, cada vez que la lleve se acordara de ti. No es muy dif&iacute;cil de hacer pero dependiendo de la t&eacute;cnica que utilices para hacerla y el n&uacute;mero de hilos con la que la quieras hacer vas a tardar m&aacute;s o menos.</p>
<p>Normalmente se realizan de dos o tres colores pero realmente puedes utilizar tantos colores como hilos. Aqu&iacute; te vamos a ense&ntilde;ar vídeos y tutoriales de pulseras de hasta tres colores distintos.</p>
<p>Cuando realices la pulsera debes tener en cuenta que cuantos m&aacute;s hilos utilices m&aacute;s ancha ser&aacute;. Por lo tanto si la realizas con tres hilos ser&aacute; una pulsera bastante estrecha,pero tambi&eacute;n ser&aacute; m&aacute;s f&aacute;cil de realizar y ser&aacute; m&aacute;s dif&iacute;cil que te equivoques haci&eacute;ndola.</p>
<p>&nbsp;</p>
<div>
<h1>Material necesario para hacer pulseras de la amistad</h1>
</div>
<p>El material necesario para realizar las pulseras ser&aacute;n el n&uacute;mero de hilos con los que quieras realizarla ,unas tijeras y un imperdible para sujetar los hilos.</p>
<p>Si le quieres dar un acabado m&aacute;s pulido deber&aacute;s comprar terminales de pulsera, broches y argollas. Ya no te har&aacute; falta el imperdible por que podr&aacute;s sujetar los hilos con uno de los terminales de pulsera. M&aacute;s adelante te ense&ntilde;amos a como realizarlo.</p>
<p>El hilo que deber&aacute;s comprar debe de ser hilo encerado. El hilo normal de tejer no se puede utilizar ya que se desfilachar&iacute;a, debe de ser un hilo m&aacute;s resistente. Hay muchas tiendas online donde puedes comprarlo y seguro que cerca de tu casa hay alguna mercer&iacute;a cerca donde comprarlos. A continuaci&oacute;n te dejamos un link donde puedes comprar los hilos encerados (https://amzn.to/39Q7IVE).</p>
<p>&nbsp;</p>
<div>
<h1>Tipos de pulseras</h1>
</div>
<p>Hay varios tipos de pulseras , la m&aacute;s popular y com&uacute;n es la pulsera trenzada. En el siguiente vídeo se muestran diferentes tipos de las pulseras m&aacute;s comunes y como realizarlas.</p>
<p><iframe class="video" title="1010"src="https://www.youtube.com/embed/raa7HNr1D5E" data-mce-fragment="1"></iframe></p>
<p>&nbsp;</p>
<h3>Pulsera trenzada</h3>
<p>En el siguiente vídeo se muestra como realizar una pulsera trenzada de nueve hilos y de tres colores distintos</p>
<p><iframe class="video" title="1011" src="https://www.youtube.com/embed/Gr11mVppXU8"  data-mce-fragment="1"></iframe></p>
<p>&nbsp;</p>
<div>
<h1>Los acabados de la pulsera</h1>
</div>
<p>Si quieres darle un acabado m&aacute;s pulido a tu pulsera deber&aacute;s utilizar terminales de pulsera, broches y argollas, como te hemos mencionado antes. Tambi&eacute;n puedes utilizar terminales de pulsera imantados. En este caso no te har&iacute;an falta ni broches ni argollas.</p>
<p><iframe class="video" title="1012" src="https://www.youtube.com/embed/-iXUPpc_nuw" data-mce-fragment="1"></iframe></p>

</div>
    <div style={{ height:`300px`}}>
    </div>
  </Layout>
)

export default PulseraAmiga
